import React from 'react';
import {ConfigProvider} from 'antd';
import LocalStorageContext, {localStorageDefaultValue} from './components/localStorage';
import {antdProvider} from './components/antdForm';
const Context: React.FC = (props) => {
  return (
    <ConfigProvider {...antdProvider}>
      <LocalStorageContext.Provider value={localStorageDefaultValue}>
        {props.children}
      </LocalStorageContext.Provider>
    </ConfigProvider>

  );
};
export default Context;
